<style lang="scss" scoped>
@import "./animate.scss";
.pptEdit {
  &_preview {
    // opacity: 0;
    background-color: #dadada;
    position: fixed;
    z-index: 1999;
    width: 100%;
    height: 100%;
    display: block;
    left: 0;
    top: 0;
    align-items: center;
    .animated {
        opacity: 1;
        animation-duration: 1s;
        animation-fill-mode: both;
    }
    &_box {
      width: 80%;
      height: 0;
      padding-top: calc(9 / 16 * 100%);
      position: relative;
      margin: 0;
    }
    &_boxinner {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    &_text {
      position: absolute;
      display: flex;
      opacity: 0;
    }
    &_img {
      position: absolute;
      display: block;
      width: 100%;
      opacity: 0;
    }
  }
  .el-carousel {
    width: 100%;
    height: 100%;
    // display: flex;
    flex-direction: column;
    align-items: center;
    /deep/ .el-carousel__container {
      height: 100%;
      > button {
        display: none;
      }
      .el-carousel__item {
        display: inline-flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}
</style>

<template>
    <div class="pptEdit_preview" @click="goNext" :style="`
        background-color:${backgroundColor}
        background-image:${backgroundImg}
        `">
        <!-- ppt预览模块 -->
        <el-carousel ref="carousel" :autoplay="false" trigger="click">
            <el-carousel-item v-for="(ite,inde) in previewList" :key="inde" v-if="!ite.hidden">
                <!-- 模块渲染 -->
                <!-- 默认16:9 -->
                <div class="pptEdit_preview_box">
                    <div class="pptEdit_preview_boxinner">
                        <template v-for="(item,index) in ite.virrtulDom">
                            <!--文字类型  -->
                            <p v-if="item.type===0"  class="pptEdit_preview_text" :key="index"
                                :class="[`${animateList[item.animate]}`,item.startAnimate?'animated':'']"
                                :style="`left:${item.x}%;
                                top:${item.y}%;
                                transform:translate(-50%,-50%) rotate(${item.rotate}deg);
                                z-index:${item.zIndex};
                                width:${item.width}%;
                                height:${item.height}%;
                                align-items:${item.alignItem};
                                justify-content:${item.textAlign};
                                text-indent:${item.textIndent}px;
                                line-height:${item.lineHeight};
                                text-decoration-line:${item.fontThrough} ${item.fontSolild};
                                color:${item.color};
                                font-size:${item.fontSize}px;
                                font-style:${item.fontOblique};
                                font-weight:${item.fontWeight};
                                background-color:${item.backgroundColor}
                                `">
                                {{item.content}}
                            </p>
                            <!--图片类型  -->
                            <img  class="pptEdit_preview_img" v-else  :src="item.content" alt="图片组件" :key="index"
                                :style="`left:${item.x}%;
                                top:${item.y}%;
                                transform:translate(-50%,-50%) rotate(${item.rotate}deg);
                                z-index:${item.zIndex};
                                width:${item.width}%;
                                `">
                        </template>
                    </div>
                </div>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
name: "pptPreview",
  props: {
    backgroundImg:{
               type:String,
               default:''
           },
           backgroundColor:{
               type:String,
               default:''
           }
  },
  computed: {
    ...mapGetters(["getPreviewList"])
  },
  data() {
    return {
      // 需要展示的ppt对象
      previewList: [
        {
          // 背景图片
          backgroundImg: require("./template/double-subtitle.png"),
          // 背景颜色
          backgroundColor: "#fff",
          // 是否处于编辑状态（点击后处于编辑状态，对应class样式--editing）
          editing: false,
          // 是否被隐藏（隐藏状态预览时不被展示，对应class样式--hidden）
          hidden: false,
          // 虚拟dom集合
          virrtulDom: []
        }
      ],
      // 内置动画库
      animateList: {
        "1": "fadeIn",
        "2": "fadeIn slower",
        "3": "fadeInUpBig",
        "4": "erasureIn slow",
        "5": "rotateIn",
        "6": "zoomIn slow",
        "7": "zoomIn",
        "8": "frameIn slow",
        "9": "fadeInUpBig slow",
        "-1": "fadeOut fast",
        "-2": "fadeOut",
        "-3": "fadeOutUpBig",
        "-4": "erasureOut slow",
        "-5": "rotateOut",
        "-6": "zoomOut slow",
        "-7": "zoomOut",
        "-8": "frameOut slow",
        "-9": "fadeOutUpBig slow"
      },
      currentClickIndex: 0
    };
  },
  methods: {
    /**
     * @description 下一步
     * @function goNext
     */
    goNext() {
      let flag = 0;
      for (let item of this.previewList[this.currentClickIndex].virrtulDom) {
        if (item.animate < 0) {
          item.animate = 0 - item.animate;
          item.startAnimate=true;
          flag++;
          break;
        }
      }
      if (flag == 0) {
        this.$refs.carousel.next();
        this.currentClickIndex++;
        if (this.currentClickIndex == this.previewList.length) {
          this.currentClickIndex = 0;
        }
      }
    }
  },
  mounted() {
    let self = this;
    let previewList = JSON.parse(JSON.stringify(this.getPreviewList));
    for (const item of previewList) {
      for (const ite of item.virrtulDom) {
        ite.animate = 0 - ite.animate;
        //添加animateed
        ite.startAnimate = ite.animate===0?true:false;
      }
    }
    this.previewList = previewList;
    document.onkeydown = event => {
      let e = event || window.event || arguments.callee.caller.arguments[0];
      if (e && e.keyCode == 37) {
        //左
        let flag = 0;
        let obj = this.previewList[this.currentClickIndex].virrtulDom;
        for (let i = obj.length - 1; i >= 0; i--) {
          if (obj[i].animate > 0) {
            obj[i].animate = 0 - obj[i].animate;
            flag++;
            break;
          }
        }
        if (flag == 0) {
          self.$refs.carousel.prev();
          this.currentClickIndex--;
          if (this.currentClickIndex == -1) {
            this.currentClickIndex = this.previewList.length - 1;
          }
        }
      }
      if (e && e.keyCode == 39) {
        //右
        this.goNext();
      }
    };
    // 每次进入获取最新的getter函数
  },
  activated() {}
};
</script>

