    &_main{
        height: 100%;
        width: 100%;
        &_text{
            position: absolute;
            left: 50%;
            top:50%;
            z-index: 0;
            width: 300px;
            height: 100px;
            cursor:move;
            border: 2px dashed #999;
            display: flex;
            margin: 0;
            user-select:none;
            // font-style: oblique;
            // font-weight: 700;
            // align-items: flex-start;
            // text-align: center;
            // text-indent: 2px;
            // line-height: 1;
            // text-decoration-line: line-through underline;
            // color: #333;
            // font-size: 12px;
        }
        &_imgBox{
            display: block;
            position: absolute;  
            cursor:move;
            border: 2px dashed #999;
            &::after{
                content: "";
