</style>

<template>
    <div class="edit_main_editingArea">
        <template v-for="(item,index) in domList">
            <!--文字类型  -->
            <p  :class="{'edit_main--editing':item.editing}" @mousedown="moduleDrag(index)" @mouseup="moduleUp" @click="moduleClick(index,item)" class="edit_main_text" v-if="item.type===0" :key="index"
                :style="`left:${item.x}%;
                top:${item.y}%;
                transform:translate(-50%,-50%) rotate(${item.rotate}deg);
                z-index:${item.zIndex};
                width:${item.width}%;
                height:${item.height}%;
                align-items:${item.alignItem};
                justify-content:${item.textAlign};
                text-indent:${item.textIndent}px;
                line-height:${item.lineHeight};
                text-decoration-line:${item.fontThrough} ${item.fontSolild};
                color:${item.color};
                font-size:${item.fontSize}px;
                font-style:${item.fontOblique};
                font-weight:${item.fontWeight};
                background-color:${item.backgroundColor}
                `">
                {{item.content}}
            </p>
            <!--图片类型  -->
            <div v-else class="edit_main_imgBox" @mousedown="moduleDrag(index)" @mouseup="moduleUp" :key="index"
                @ondrag="moduleDrag"  :class="{'edit_main--editing':item.editing}" @click="moduleClick(index,item)"
                :style="`left:${item.x}%;
                top:${item.y}%;
                transform:translate(-50%,-50%) rotate(${item.rotate}deg);
                z-index:${item.zIndex};
                width:${item.width}%;
                `">
                <img  class="edit_main_img"  :src="item.content" alt="图片组件" >
            </div>
        </template>
    </div>
</template>
<script>
import {throttle} from '../../utils';
export default {
    name:'Edit',
    props:{
        domList:{
            type:Array,
            default(){
                return {
                    editing:false,//是否处于编辑状态
                    type:0,//组件类型 0=文本框 1=图片 2=表格
                    content:'我是示例',//内容 文本框（文字内容）图片（src）
                    x:50,//x轴
                    y:50,//y轴
                    width:20,//宽度
                    height:20,//高度
                    rotate:0,//旋转角度
                    zIndex:0,//层高等级
                    // 文本框特有属性值
                    fontSize:0,//字体大小
                    color:'#000',//字体颜色
                    lineHeight:1,//行高
                    textAlign:'center',//center left right
                    textIndent:0,//字体后移
                    fontWeight:400,//字体加粗
                    fontOblique:'',//字体倾斜
                    fontThrough:'',//字体中划线
                    fontSolild:'',//字体下划线
                    alignItem:'flex-start',//垂直效果
                    effectsList:[],//效果集合
                }
            },
        }
    },
    data(){
        return{
            
        }
    },
    methods:{
        /**
         * @description 当前模块被点击函数
         * @function moduleClick
         * @param  {type} index {当前模块的index值}
         * @return {type} {description}
         */
        moduleClick(index){
            this.$emit('moduleClick',index);
        },
        /**
         * @description 模块拖动
         * @function moduleDrag
         * @return {type} {description}
         */
        moduleDrag(index,e){
            let box=document.querySelector(".pptEdit_main_editBox");
            document.onmousemove=(e)=>{
                let flag=this.getClinent();
                    // console.log((e.clientX-flag.left)/flag.width);
                    // console.log((e.clientY-flag.top)/flag.height);
                    this.$emit('moduleDrop',{
                        index:index,
                        x:(e.clientX-flag.left)/flag.width,
                        y:(e.clientY-flag.top)/flag.height
                    })
            }; 
            document.onmouseup = (e)=> {
                document.onmousemove = null
                document.onmouseup = null
            }  
        },
        /**
         * @description 获取父元素的宽和高
         * @function moduleDrag
         * @return {type} {description}
         */
        getClinent(){
            let box=document.querySelector(".pptEdit_main_editBox");
            let top=box.offsetTop,
                left=box.scrollLeft;
            let cur=box.offsetParent;
            while(cur != null){
                top += cur.offsetTop;
                left += cur.offsetLeft;
                cur = cur.offsetParent;
            }
            return {
                left:left,
                top:top,
                width:box.offsetWidth,
                height:box.offsetHeight
            }
        },
        /**
         * @description 清楚全局的拖动参数
         * @function moduleUp
         * @return {type} {description}
         */
        moduleUp(){

        },
    },
    mounted(){

    }
}
</script>

