<style lang="scss">
@import "./common.scss";
.pptEdit {
  height: calc(100vh - 84px);
  // 头部操作区域样式
  &_header {
    padding: 0;
    &_colorPicker {
      display: inline-flex;
      align-items: center;
      margin-left: 20px;
      height: 28px;
      border-left: 1px solid #dadada;
      padding-left: 20px;
      font-size: 13px;
      color: #f80;
    }
    &_textSettingBox {
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;
      width: calc(100% - 310px);
    }
  }
  //  弹出框样式
  &_dialog {
    .el-dialog__body {
      padding: 15px;
    }
    .el-dialog__footer {
      padding: 0;
    }
    &_buttonList {
      text-align: right;
      padding: 20px 30px;
    }
    &_selectList {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 0;
    }
    &_single {
      cursor: pointer;
      list-style: none;
      width: 150px;
      height: 150px;
      border-radius: 3px;
      box-sizing: border-box;
      border: 1px solid rgba(0, 0, 0, 0.3);
      background-color: #dadada;
      transition: all 0.2s ease-in;
      &:hover {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.32),
          0 0 6px 0 rgba(0, 0, 0, 0.14);
      }
      margin-bottom: 10px;
    }
    &_img {
      display: block;
      height: 100%;
      margin: 0 auto;
    }
    &_imgList {
      :after {
        content: "";
        display: block;
        clear: both;
      }
      max-height: 500px;
      overflow-y: auto;
    }
    &_imgSingle {
      float: left;
      width: 30%;
      display: block;
      box-sizing: border-box;
      border: 1px solid #dadada;
      &:nth-of-type(3n-1) {
        margin: 0 5%;
      }
      margin-bottom: 20px;
    }
  }
  &_container {
    height: calc(100% - 108px);
  }
  // 侧边ppt缩略图列表
  &_aside {
    border-right: 1px solid #dedede;
    background: #f1f1f1;
    height: 100%;
    position: relative;
    &_list {
      height: 100%;
      box-sizing: border-box;
      margin: 0;
      padding: 20px;
      overflow-y: auto;
      position: relative;
      padding-bottom: 50px;
    }
    &_single {
      list-style: none;
      width: 100%;
      position: relative;
      height: 150px;
      border-radius: 3px;
      box-sizing: border-box;
      border: 1px solid rgba(0, 0, 0, 0.3);
      background-color: #dadada;
      transition: all 0.2s ease-in;
      margin-bottom: 15px;
      overflow: hidden;
      display: flex;
      align-items: center;
      cursor: pointer;
      &:hover {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.32),
          0 0 6px 0 rgba(0, 0, 0, 0.14);
      }
      // 编辑状态样式
      &--editing {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.32),
          0 0 6px 0 rgba(0, 0, 0, 0.14);
      }
      // 隐藏状态样式
      &--hidden {
        opacity: 0.5;
      }
    }
    &_img {
      display: block;
      width: 100%;
    }
    &_elseSelect {
      position: absolute;
      right: 0;
      bottom: -2px;
    }
    &_addPPT {
      position: absolute;
      bottom: 15px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  // 主要操作区域
  &_main {
    background: #fff;
    box-sizing: border-box;
    padding: 0;
    display: flex;
    &_content {
      background-color: #edebe9;
      flex: 1;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
    }
    // 编辑区域外层包裹用来控制百分比
    &_editBox {
      width: calc(100% - 60px);
      height: 0;
      position: absolute;
      padding-top: calc(100% * 9 / 16);
      .box-card {
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        background-color: transparent;
        top: 0;
      }
    }
    // 右侧具体属性编辑区域
    &_rightAside {
      height: 100%;
      flex-shrink: 0;
      flex-grow: 0;
      width: 250px;
      box-sizing: border-box;
      padding: 10px;
      border-left: 1px solid #dadada;
      background-color: #f1f1f1;
      overflow-y: auto;
    }
    &_editForm {
      .el-form-item {
        margin-bottom: 8px;
      }
      .el-form-item__content {
        font-size: 12px;
        display: flex;
        // justify-content: space-between;
        flex-wrap: wrap;
      }
      .el-form-item__label {
        font-size: 12px;
      }
      .el-radio-group {
        width: 100%;
        margin-bottom: 5px;
      }
    }
    &_editSingle {
      display: flex;
      flex-direction: column;
      align-items: center;
      &:first-child {
        margin-right: 15px;
      }
    }
    &_description {
      line-height: 20px;
      font-size: 12px;
      color: #666;
    }
  }
}
</style>


<template>
    <el-container class="pptEdit">
        <!-- 模板选择弹出框 -->
        <el-dialog
            title="选择模板"
            :visible.sync="selectTemplate"
            width="520px"
            :close-on-click-modal='false'
            class="pptEdit_dialog"
            >
            <!-- 可选择的模板列表 -->
            <ul class="pptEdit_dialog_selectList">
                <li class="pptEdit_dialog_single" 
                    @click="addTemplate(index)"
                    v-for="(item,index) in templateList" :key="index">
                    <img class="pptEdit_dialog_img" :src="item.backgroundImg" alt="缩略图">
                </li>  
            </ul>
            <span slot="footer">
            </span>
        </el-dialog>
        <!-- 图片插入方式选择弹出框 -->
        <el-dialog
            title="选择图片插入方式"
            :visible.sync="addImgAction"
            width="700px"
            :close-on-click-modal='false'
            class="pptEdit_dialog"
            >
            <el-tabs type="border-card">
                <el-tab-pane label="上传本地图片">
                    <el-upload
                        class="upload-demo"
                        drag
                        :show-file-list="false"
                        name='imgtext'
                        action="/form"
                        :before-upload="beforeAvatarUpload"
                        :on-success="successUpload"
                    >
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    <div class="el-upload__tip" slot="tip">只能是 JPG或者PNG 格式，图片大小不能超过 500KB!</div>
                    </el-upload>
                </el-tab-pane>
                <el-tab-pane label="使用网络URL">
                    <el-input placeholder="请输入内容" v-model="onlineImgUrl">
                    </el-input>
                    <div class="pptEdit_dialog_buttonList">
                        <el-button @click="addImgAction = false">取 消</el-button>
                        <el-button type="primary" @click="addImgArea(1,onlineImgUrl)">确 定</el-button>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="使用在线图片库">
                    <div class="pptEdit_dialog_imgList">
                         <img v-for="(item,index) in onlineImgList" @click="addImgArea(1,item)" :key="index" :src="item" alt="在线图片" class="pptEdit_dialog_imgSingle">
                    </div>
                </el-tab-pane>
            </el-tabs>
            <span slot="footer">
            </span>
        </el-dialog>
        <el-header class="pptEdit_header" height="">
            <!-- 顶部操作栏区域 -->
            <el-tabs type="border-card">
                <el-tab-pane label="文件">
                    <!-- 保存 -->
                    <el-button type="primary" size="mini">
                        <svg-icon icon-class="save"  class-name=''/>保存
                    </el-button>
                    <!-- 下载为 -->
                    <el-button size="mini" type="" icon='el-icon-download'>下载</el-button>
                </el-tab-pane>
                <el-tab-pane label="开始">
                    <el-button-group class="pptEdit_marginRight">
                        <el-tooltip class="item" effect="dark" content="撤销" placement="bottom">
                            <el-button size="mini" type="" @click="selectTemplate=true">
                                <!-- 撤销 -->
                                <svg-icon icon-class="goBack"  class-name='' />
                            </el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="删除" placement="bottom">
                            <el-button size="mini" type="" >
                                <!-- 删除 -->
                                <svg-icon :disabled="currentSlideIndex===-1" icon-class="delet"  class-name='' />
                            </el-button>
                        </el-tooltip>
                    </el-button-group>
                    <!-- 新建幻灯片 -->
                    <el-tooltip class="item" effect="dark" content="新建幻灯片" placement="bottom">
                        <el-button  size="mini" @click="selectTemplate=true" type="primary" circle><svg-icon icon-class="addPpt"  class-name='' /></el-button>
                    </el-tooltip>
                    <!-- 隐藏幻灯片 -->
                    <el-tooltip class="item" effect="dark" content="隐藏幻灯片" placement="bottom">
                        <el-button :disabled="currentSlideIndex===-1" size="mini" type="" circle><svg-icon icon-class="hidden"  class-name='' /></el-button>
                    </el-tooltip>
                    <!-- 默认字体设置 -->
                    <div class="pptEdit_header_textSettingBox">
                        <!-- 文字基础属性设置 -->
                            <el-tag type="info" class="pptEdit_marginLeft">字体大小</el-tag>
                            <el-tooltip  effect="dark" content="字体大小设置" placement="bottom">
                                <el-input-number class="pptEdit_commonNumberInput" size="mini" v-model="textSetting.fontSize" controls-position="right"  :min="0" :max="1000"></el-input-number>
                            </el-tooltip>
                            
                            <el-tooltip  effect="dark" content="字体颜色选择" placement="bottom">
                                <el-color-picker v-model="textSetting.color" size="small" show-alpha></el-color-picker>
                            </el-tooltip>
                        <!-- 文字样式设置 -->
                        <el-checkbox-group class="pptEdit_marginLeft" @change="commonTextEffectChange" v-model="textSetting.effectsList" size="mini">
                            <el-checkbox-button  label="stretch" >
                                <svg-icon icon-class="stretch"  class-name='' />       
                            </el-checkbox-button>
                            <el-checkbox-button  label="oblique" >
                                <svg-icon icon-class="oblique"  class-name='' />
                            </el-checkbox-button>
                            <el-checkbox-button  label="solild" >
                                <svg-icon icon-class="solild"  class-name='' />
                            </el-checkbox-button>
                            <el-checkbox-button  label="through" >
                                <svg-icon icon-class="through"  class-name='' />
                            </el-checkbox-button>
                        </el-checkbox-group>  
                        <!-- 水平对齐设置 -->
                        <el-radio-group class="pptEdit_marginLeft" v-model="textSetting.textAlign" size="mini">
                            <el-radio-button label="flex-start">
                                <svg-icon icon-class="left"  class-name='' />
                            </el-radio-button>
                            <el-radio-button label="center">
                                <svg-icon icon-class="center"  class-name='' />
                            </el-radio-button>
                            <el-radio-button label="flex-end">
                                <svg-icon icon-class="right"  class-name='' />
                            </el-radio-button>
                        </el-radio-group>
                        <!-- 垂直对齐设置 -->
                        <el-radio-group class="pptEdit_marginLeft" v-model="textSetting.alignItem" size="mini">
                            <el-radio-button label="flex-start">
                                <svg-icon icon-class="flexStart"  class-name='' />
                            </el-radio-button>
                            <el-radio-button label="center">
                                <svg-icon icon-class="alignCenter"  class-name='' />
                            </el-radio-button>
                            <el-radio-button label="flex-end">
                                <svg-icon icon-class="flexEnd"  class-name='' />
                            </el-radio-button>
                        </el-radio-group>
                        <!-- 行高设置 -->
                        <el-tag  type="info" class="pptEdit_marginLeftBig">行高</el-tag>
                        <el-tooltip  effect="dark" content="行高设置" placement="bottom">
                            <el-input-number class="pptEdit_commonNumberInput" size="mini" v-model="textSetting.lineHeight" controls-position="right"  :step="0.1"  :min="1" :max="100"></el-input-number>
                        </el-tooltip>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="插入">
                    <!-- 新建幻灯片 -->
                    <el-button  size="mini" @click="selectTemplate=true" type="primary"><svg-icon icon-class="addPpt"  class-name='' />&nbsp;新建幻灯片</el-button>
                    <el-button-group class="pptEdit_marginLeftBig"> 
                        <el-button :disabled="currentSlideIndex===-1"  size="mini" type="" @click="addImgAction=true">
                            <!-- 图片 -->
                            <svg-icon icon-class="addImg"  class-name='' />
                            图片
                        </el-button>
                        <el-button :disabled="currentSlideIndex===-1" size="mini" type="" >
                            <!-- 表格 -->
                            <svg-icon icon-class="tableList"  class-name='' />
                            表格
                        </el-button>
                        <el-button :disabled="currentSlideIndex===-1" size="mini" type="" @click="addTextArea">
                            <!-- 文本框 -->
                            <svg-icon icon-class="txtArea"  class-name='' />
                            文本框
                        </el-button>
                    </el-button-group>
                </el-tab-pane>
                <el-tab-pane label="设计">
                    <el-button size="mini" type="primary" >
                        <!-- 幻灯片大小 -->
                        <svg-icon icon-class="pptSize"  class-name='' />
                        幻灯片大小
                    </el-button>
                    <el-button size="mini" type="" >
                        <!-- 设置背景图片 -->
                        <svg-icon icon-class="backgroundImg"  class-name='' />
                        背景图片
                    </el-button>
                    <!-- 设置背景颜色 -->
                    <div class="pptEdit_header_colorPicker">
                        <!-- <svg-icon icon-class="colorPicker"  class-name='' /> -->
                        背景颜色选择
                        <!-- 背景颜色 -->
                        <el-color-picker class="pptEdit_marginLeft" size='mini' v-model="pptSetting.backgroundColor"></el-color-picker>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="视图">
                    <!-- 从头开始播放 -->
                    <el-button size="mini" :disabled="currentSlideIndex===-1" type="primary" @click="openPreview"> <svg-icon icon-class="autoPlay"  class-name='' /> 从头开始播放</el-button>
                    <!-- 从当前幻灯片开始播放 -->
                    <el-button size="mini" :disabled="currentSlideIndex===-1" @click="openPreview(currentSlideIndex)"><svg-icon icon-class="play"  class-name='' />从当前位置开始播放</el-button>
                </el-tab-pane>
            </el-tabs>
        </el-header>
        <el-container class="pptEdit_container">
            <!-- 使用滑动组件 -->
            <el-aside class="pptEdit_aside" width="200px">
                <ul class="pptEdit_aside_list">
                    <!-- 单个卡片列表 -->
                    <!-- 状态列表：1.被编辑状态--editing 2.隐藏状态--hidden-->
                    <li class="pptEdit_aside_single" 
                        @click="slideClick(index)"
                        :class="{'pptEdit_aside_single--editing':item.editing,'pptEdit_aside_single--hidden':item.hidden}" 
                        v-for="(item,index) in sliderList" :key="index">
                        <img @dblclick="addTextArea" class="pptEdit_aside_img" :src="item.backgroundImg" alt="缩略图">
                        <el-dropdown trigger="click" @command="sliderCommand" type="primary" size="mini" split-button class="pptEdit_aside_elseSelect">
                                选项
                            <!-- 额外选项操作 -->
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item :command="`[0,${index}]`">新建</el-dropdown-item>
                                <el-dropdown-item :command="`[1,${index}]`">上移</el-dropdown-item>
                                <el-dropdown-item :command="`[1,${index+1}]`">下移</el-dropdown-item>
                                <el-dropdown-item :command="`[2,${index},0]`">隐藏</el-dropdown-item>
                                <el-dropdown-item :command="`[2,${index},1]`">显示</el-dropdown-item>
                                <el-dropdown-item :command="`[3,${index}]`">删除</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </li>
                </ul>
                <el-button @click="selectTemplate=true" class="pptEdit_aside_addPPT" type="primary" icon="el-icon-circle-plus-outline">新增幻灯片</el-button>
            </el-aside>
            <el-main class="pptEdit_main">
                <!-- 中间具体内容操作区域 -->
                <section class="pptEdit_main_content">
                    <!-- 默认占比16：9 -->
                    <div class="pptEdit_main_editBox"
                        :style="`
                        background-color:${pptSetting.backgroundColor}
                        background-image:${pptSetting.backgroundImg}
                        `">
                        <el-card  class="box-card">
                            <!-- rend函数渲染 只有slider列表具体slider被点击时才会出现，全部删除时自动设置为0-->
                            <edit v-if="currentSlideIndex>-1" :domList="sliderList[currentSlideIndex].virrtulDom" v-on:moduleClick="moduleClick" @moduleDrop="moduleDrop"></edit>
                        </el-card>
                    </div>
                </section>
                <!-- 右侧属性编辑区 -->
                <aside class="pptEdit_main_rightAside">
                    <el-form v-show="currentModuleIndex>-1" class="pptEdit_main_editForm" ref="form" :model="currentModule" label-width="40px">
                        <!-- 通用属性设置 1.位置（百分比%）2.长宽 （百分比%）3.变换（旋转，deg）-->
                        <el-form-item label="位置">
                            <div class="pptEdit_main_editSingle">
                                <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.x" controls-position="right"  :min="0" :max="100"></el-input-number>
                                <!-- 属性说明文字 -->
                                <span class="pptEdit_main_description">X</span>
                            </div>
                            <div class="pptEdit_main_editSingle">
                                <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.y" controls-position="right"  :min="0" :max="100"></el-input-number>
                                <span class="pptEdit_main_description">Y</span>
                            </div>
                        </el-form-item>
                        <el-form-item label="大小">
                            <div class="pptEdit_main_editSingle">
                                <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.width" controls-position="right"  :min="0" :max="100"></el-input-number>
                                <span class="pptEdit_main_description">宽度</span>
                            </div>
                            <div v-show="currentModule.type!=1" class="pptEdit_main_editSingle">
                                <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.height" controls-position="right"  :min="0" :max="100"></el-input-number>
                                <span class="pptEdit_main_description">高度</span>
                            </div>
                        </el-form-item>
                        <el-form-item label="变换">
                            <div class="pptEdit_main_editSingle">
                                <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.rotate" controls-position="right"  :min="-360" :max="360"></el-input-number>
                                <span class="pptEdit_main_description">旋转</span>
                            </div>
                            <div class="pptEdit_main_editSingle">
                                <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.zIndex" controls-position="right"  :min="0" :max="100"></el-input-number>
                                <span class="pptEdit_main_description">层级</span>
                            </div>
                        </el-form-item>

                        <!-- 文本框私有属性设置区域 -->
                        <template v-if="currentModule.type==0">
                            <el-form-item label="字体">
                                <div class="pptEdit_main_editSingle">
                                    <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.fontSize" controls-position="right"  :min="0" :max="1000"></el-input-number>
                                    <span class="pptEdit_main_description">字体大小</span>
                                </div>
                                <div class="pptEdit_main_editSingle">
                                   <el-color-picker @change="updateSliderBackground" v-model="currentModule.color" size="small" show-alpha></el-color-picker>
                                </div>    
                            </el-form-item> 
                            <el-form-item label="背景">
                                   <el-color-picker @change="updateSliderBackground" v-model="currentModule.backgroundColor" size="small" show-alpha></el-color-picker>
                            </el-form-item> 
                            <!-- 文字样式效果设置 加粗 ，斜体 ，下划线 ，中划线 -->
                            <el-form-item label="效果">
                                <el-checkbox-group @change="textEffectChange" v-model="currentModule.effectsList" size="mini">
                                    <el-checkbox-button  label="stretch" >
                                        <svg-icon icon-class="stretch"  class-name='' />       
                                    </el-checkbox-button>
                                    <el-checkbox-button  label="oblique" >
                                        <svg-icon icon-class="oblique"  class-name='' />
                                    </el-checkbox-button>
                                    <el-checkbox-button  label="solild" >
                                        <svg-icon icon-class="solild"  class-name='' />
                                    </el-checkbox-button>
                                    <el-checkbox-button  label="through" >
                                        <svg-icon icon-class="through"  class-name='' />
                                    </el-checkbox-button>
                                </el-checkbox-group>     
                            </el-form-item> 
                            <!-- 水平和垂直方向的对齐设置 -->
                            <el-form-item label="对齐">
                                <el-radio-group @change="updateSliderBackground" v-model="currentModule.textAlign" size="mini">
                                    <el-radio-button label="flex-start">
                                        <svg-icon icon-class="left"  class-name='' />
                                    </el-radio-button>
                                    <el-radio-button label="center">
                                        <svg-icon icon-class="center"  class-name='' />
                                    </el-radio-button>
                                    <el-radio-button label="flex-end">
                                        <svg-icon icon-class="right"  class-name='' />
                                    </el-radio-button>
                                </el-radio-group>
                                <el-radio-group @change="updateSliderBackground" v-model="currentModule.alignItem" size="mini">
                                    <el-radio-button label="flex-start">
                                        <svg-icon icon-class="flexStart"  class-name='' />
                                    </el-radio-button>
                                    <el-radio-button label="center">
                                        <svg-icon icon-class="alignCenter"  class-name='' />
                                    </el-radio-button>
                                    <el-radio-button label="flex-end">
                                        <svg-icon icon-class="flexEnd"  class-name='' />
                                    </el-radio-button>
                                </el-radio-group>
                            </el-form-item> 
                            <!-- 增强效果，首行空格和行高更改 -->
                            <el-form-item label="增强">
                                <div class="pptEdit_main_editSingle">
                                    <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.textIndent" controls-position="right"  :min="-1000" :max="1000"></el-input-number>
                                    <span class="pptEdit_main_description">占位</span>
                                </div>
                                <div class="pptEdit_main_editSingle">
                                    <el-input-number @change="updateSliderBackground" class="pptEdit_commonNumberInput" size="mini" v-model="currentModule.lineHeight" controls-position="right"  :step="0.1"  :min="1" :max="100"></el-input-number>
                                    <span class="pptEdit_main_description">行高</span>
                                </div>    
                            </el-form-item> 
                        </template>
                        <el-form-item label="动画">
                            <el-select v-model="currentModule.animate" placeholder="请选择">
                                <el-option
                                v-for="item in animateList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item> 
                        <el-form-item label="内容">
                            <el-input
                                type="textarea"
                                :autosize="{ minRows: 2, maxRows: 4}"
                                placeholder="请输入内容"
                                @change="updateSliderBackground"
                                v-model="currentModule.content">
                            </el-input>
                        </el-form-item> 
                    </el-form>
                </aside>
            </el-main>
        </el-container>
        <ppt-preview :backgroundImg="pptSetting.backgroundImg" :backgroundColor="pptSetting.backgroundColor" v-if="showPreview" ></ppt-preview>
    </el-container>
</template>

<script>
import Edit from "./edit";
import PptPreview from "./pptPreview";
import { templateList } from "./template.js";
import { pptEdit } from "@/api/pptEdit";
import html2canvas from "html2canvas";
import { debounce } from "@/utils";
export default {
  data() {
    return {
      showPreview: false, //展开预览按钮
      addImgAction: false, //是否显示图片插入方式
      selectTemplate: false, //是否展示模板选择器
      needPositionAdd: false, //从特殊位置插入模板
      onlineImgUrl: "", //网络图片地址
      base64Url: "", //本地图片地址
      onlineImgList: [], //在线图片地址集合
      // ppt通用属性设置
      pptSetting: {
        height: "",
        width: "",
        backgroundImg: "",
        backgroundColor: ""
      },
      // 文本统一属性设置（初始化作用）
      textSetting: {
        fontSize: 20, //字体大小
        color: "#000", //字体颜色
        lineHeight: 1, //行高
        textAlign: "center", //center left right
        textIndent: 0, //字体后移
        fontWeight: 400, //字体加粗
        fontOblique: "", //字体倾斜
        fontThrough: "", //字体中划线
        fontSolild: "", //字体下划线
        alignItem: "center", //垂直效果
        effectsList: [] //效果集合
      },
      //可用模板
      templateList: templateList,
      // ppt模板数据集合（json格式生成虚拟dom节点）
      sliderList: [
        // {
        //   // 背景图片
        //   backgroundImg: require("./template/double-subtitle.png"),
        //   // 背景颜色
        //   backgroundColor: "#fff",
        //   // 是否处于编辑状态（点击后处于编辑状态，对应class样式--editing）
        //   editing: false,
        //   // 是否被隐藏（隐藏状态预览时不被展示，对应class样式--hidden）
        //   hidden: false,
        //   // 虚拟dom集合
        //   virrtulDom: [
        //     {
        //       editing: false, //是否处于编辑状态
        //       type: 0, //组件类型 0=文本框 1=图片 2=表格
        //       content: "实例", //内容 文本框（文字内容）图片（src）
        //       x: 50, //x轴
        //       y: 50, //y轴
        //       width: 20, //宽度
        //       height: 20, //高度
        //       rotate: 0, //旋转角度
        //       zIndex: 0, //层高等级
        //       animate:'',//动画效果
        //       // 文本框特有属性值
        //       fontSize: 12, //字体大小
        //       color: "#000", //字体颜色
        //       backgroundColor:"#fff",//背景颜色
        //       lineHeight: 1.5, //行高
        //       textAlign: "center", //center left right
        //       textIndent: 0, //字体后移
        //       fontWeight: 400, //字体加粗
        //       fontOblique: "", //字体倾斜
        //       fontThrough: "", //字体中划线
        //       fontSolild: "", //字体下划线
        //       alignItem: "flex-start", //垂直效果
        //       effectsList: [] //效果集合
        //     }
        //   ]
        // },
        // {
        //   backgroundImg: require("./template/double.png"),
        //   backgroundColor: "#fff",
        //   editing: false,
        //   hidden: true,
        //   virrtulDom: []
        // }
      ],
      //当前处于编辑状态的ppt对象
      currentModule: {
        x: 0, //
        y: 0, //
        width: 0, //
        height: 0, //
        rotate: 0, //
        zIndex: 0 //
      },
      //当前处于编辑状态的SliderIndex值
      currentSlideIndex: -1,
      //当前处于编辑状态的module的index值
      currentModuleIndex: -1,
      //动画效果
      animateList: [
        {
          label: "出现",
          value: "1"
        },
        {
          label: "淡出",
          value: "2"
        },
        {
          label: "飞入",
          value: "3"
        },
        {
          label: "擦除",
          value: "4"
        },
        {
          label: "翻转式由远及近",
          value: "5"
        },
        {
          label: "缩放",
          value: "6"
        },
        {
          label: "基本缩放",
          value: "7"
        },
        {
          label: "框",
          value: "8"
        },
        {
          label: "展开",
          value: "9"
        },
        {
          label: "升起",
          value: "10"
        }
      ]
    };
  },
  components: {
    Edit,
    PptPreview
  },
  methods: {
    /**
     * @description 新增模板
     * @function addTemplate
     * @param  {number} index 模板id
     */
    addTemplate(index) {
      // 是否需要从特殊位置插入模板
      let needIndex = this.needPositionAdd
        ? this.currentSlideIndex + 1
        : this.sliderList.length;
      // 该操作深拷贝，防止出现属性联动
      this.sliderList.splice(
        needIndex,
        0,
        JSON.parse(JSON.stringify(this.templateList[index]))
      );
      this.selectTemplate = false;
      this.needPositionAdd = false;
    },
    /**
     * @description 新建文本框
     * @function addTextArea
     */
    addTextArea() {
      let attribute = Object.assign(
        {},
        JSON.parse(JSON.stringify(this.textSetting)),
        {
          editing: false, //是否处于编辑状态
          type: 0, //组件类型 0=文本框 1=图片 2=表格
          content: "", //内容 文本框（文字内容）图片（src）
          x: Math.ceil(Math.random() * 100), //x轴
          y: Math.ceil(Math.random() * 100), //y轴
          width: 20, //宽度
          height: 20, //高度
          rotate: 0, //旋转角度
          zIndex: 0, //层高等级
          animate: "", //动画效果
          backgroundColor: "" //背景颜色
        }
      );
      this.sliderList[this.currentSlideIndex].virrtulDom.push(attribute);
    },
    /**
     * @description 添加图片
     * @function addImgArea
     * @param  {number} type ppt内置元素种类
     */
    async addImgArea(type, imgUrl = null) {
      // 判断当前是否有被选中的slider对象
      let attribute = Object.assign(
        {},
        {
          editing: false, //是否处于编辑状态
          type: 1, //组件类型 0=文本框 1=图片 2=表格
          content: "", //内容 文本框（文字内容）图片（src）
          x: Math.ceil(Math.random() * 100), //x轴
          y: Math.ceil(Math.random() * 100), //y轴
          width: 30, //宽度
          rotate: 0, //旋转角度
          zIndex: 0, //层高等级
          animate: ""
        }
      );
      // 三种图片插入模式
      let base64 = "";
      if (type !== 0) {
        base64 = await this.getBase64(imgUrl);
      } else {
        base64 = imgUrl;
      }
      attribute.content = base64;
      this.sliderList[this.currentSlideIndex].virrtulDom.push(attribute);
      this.addImgAction = false;
    },
    /**
     * @description 获取在线图片资源集合
     * @function getImgList
     */
    getImgList() {
      pptEdit.getImgList().then(res => {
        this.onlineImgList = res.data.urlList;
      });
    },
    /**
     * @description ppt点击函数 （清空当前编辑区域的数据）
     * @function slideClick
     * @param  {type} index {当前ppt的index值}
     */
    slideClick(index) {
      if (this.currentSlideIndex > -1) {
        this.sliderList[this.currentSlideIndex].editing = false;
      }
      this.currentSlideIndex = index;
      this.sliderList[index].editing = true;
      this.currentModule = {};
      this.currentModuleIndex = -1;
    },
    /**
     * @description 当前模块被点击函数
     * @function moduleClick
     * @param  {type} index {当前模块的index值}
     */
    moduleClick(index) {
      // 串联当前编辑区域
      this.currentModule = this.sliderList[this.currentSlideIndex].virrtulDom[
        index
      ];
      if (this.currentModuleIndex != index && this.currentModuleIndex != -1) {
        this.sliderList[this.currentSlideIndex].virrtulDom[
          this.currentModuleIndex
        ].editing = false;
      }
      this.currentModuleIndex = index;
      this.currentModule.editing = true;
    },
    /**
     * @description 位置属性设置
     * @function moduleDrop
     * @param  {object} obj 编辑组件传递过来的拖拽属性
     */
    moduleDrop(obj) {
      let dragDom = this.sliderList[this.currentSlideIndex].virrtulDom[
        obj.index
      ];
      // 边际宽高
      let maxX = 100 - dragDom.width / 2,
        maxY = 100 - dragDom.height / 2;

      let fixedX = (obj.x * 100).toFixed(2),
        fixedY = (obj.y * 100).toFixed(2);

      // 边缘检测
      if (fixedX < dragDom.width / 2 || fixedX > maxX) {
        if (fixedX < dragDom.width / 2) {
          dragDom.x = dragDom.width / 2;
        } else {
          dragDom.x = maxX;
        }
      } else {
        dragDom.x = fixedX;
      }
      if (fixedY < dragDom.height / 2 || fixedY > maxX) {
        if (fixedY < dragDom.height / 2) {
          dragDom.y = dragDom.height / 2;
        } else {
          dragDom.y = maxY;
        }
      } else {
        dragDom.y = fixedY;
      }
      this.updateSliderBackground();
    },
    /**
     * @description 字体效果更改后变化
     * @function textEffectChange
     * @param  {array} list 需要绑定的属性值集合（粗体，斜体，下划线，中划线）
     */
    textEffectChange(list) {
      let setList = new Set(list);
      this.currentModule.fontWeight = setList.has("stretch") ? 700 : 400;
      this.currentModule.fontOblique = setList.has("oblique") ? "oblique" : "";
      this.currentModule.fontSolild = setList.has("solild") ? "underline" : "";
      this.currentModule.fontThrough = setList.has("through")
        ? "line-through"
        : "";
      this.updateSliderBackground();
    },
    /**
     * @description 全局字体效果更改后变化
     * @function commonTextEffectChange
     * @param  {array} list 需要绑定的属性值集合（粗体，斜体，下划线，中划线）
     */
    commonTextEffectChange(list) {
      let setList = new Set(list);
      this.textSetting.fontWeight = setList.has("stretch") ? 700 : 400;
      this.textSetting.fontOblique = setList.has("oblique") ? "oblique" : "";
      this.textSetting.fontSolild = setList.has("solild") ? "underline" : "";
      this.textSetting.fontThrough = setList.has("through")
        ? "line-through"
        : "";
    },
    /**
     * @description 图片上传前检测
     * @function beforeAvatarUpload
     * @param  {type} file 内置文件对象
     * @return {bollean} 是否满足当前设定额检测条件
     */
    beforeAvatarUpload(file) {
      const isJPGPNG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 512 < 1;
      if (!isJPGPNG) {
        this.$message.error("上传图片只能是 JPG或者PNG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 2MB!");
      }
      return isJPGPNG && isLt2M;
    },
    /**
     * @description 成功后的回调参数
     * @function successUpload
     * @param  {type} response 成功后的毁掉参数
     */
    successUpload(response) {
      if (response.code === 200) {
        this.getBase64(response.data.url).then(res => {
          this.addImgArea(0, res); //
        });
      }
    },
    /*********** ppt对象额外操作函数 ************/
    /**
     * @description ppt右下角点击函数对应具体操作
     * @function sliderCommand
     * @param number 操作类型 0：新增slider 1：切换位置 2：切换隐藏 3：删除模板
     * @return {type} {description}
     */
    sliderCommand(command) {
      let result = JSON.parse(command);
      if (result[0] === 0) {
        this.selectTemplate = true;
        this.needPositionAdd = true;
      }
      if (result[0] === 1) {
        this.toglePosition(result[1]);
      }
      if (result[0] === 2) {
        this.toggleShow(result[1], result[2]);
      }
      if (result[0] === 3) {
        this.deleteSlider(result[1]);
      }
    },
    /**
     * @description   切换ppt图片的显示隐藏
     * @function toggleShow
     * @param  {number} index 当前被选中的sliderindex值
     * @param  {number} type 判定显示隐藏
     */
    toggleShow(index, type) {
      this.sliderList[index].hidden = type === 0 ? true : false;
    },
    /**
     * @description 切换上下移动位置
     * @function toglePosition
     * @param  {number} index 当前选中的slider index值
     */
    toglePosition(index) {
      if (index > this.sliderList.length - 1 || index - 1 < 0) {
        return false;
      }
      // 上移动
      this.sliderList.splice(index - 1, 0, this.sliderList.splice(index, 1)[0]);
      // index+1
      // 下移动
    },
    /**
     * @description 删除ppt
     * @function deleteSlider
     * @param  {type} index {description}
     * @return {type} {description}
     */
    deleteSlider(index) {
      this.$confirm("此操作将删除该PPT, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.sliderList.splice(index, 1);
          this.currentSlideIndex = -1;
          if (this.sliderList.length > 0) {
            this.slideClick(0);
          }
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /*************************/
    /**
     * @description 打开预览
     * @function openPreview
     */
    openPreview(index = 0) {
      this.enterFullScreen();
      this.$store.commit("setPreviewList", this.sliderList);
      this.showPreview = true;
    },
    /**
     * @description 进入全屏
     * @function enterFullScreen
     */
    enterFullScreen() {
      let element = document.documentElement;
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullScreen();
      }
    },
    /**
     * @description 退出全屏
     * @function outFullScreen
     */
    fullscreenchange() {
      var isFull =
        document.fullscreenEnabled ||
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      if (!isFull) {
        this.showPreview = false;
        document.onkeydown = null;
      }
    },
    /**
     * @description 获取当前图片远程rul的base64代码
     * @function getBase64
     */
    async getBase64(img) {
      let getBase64Image = (img, width, height) => {
        //width、height调用时传入具体像素值，控制大小 ,不传则默认图像大小
        let canvas = document.createElement("canvas");
        canvas.width = width ? width : img.width;
        canvas.height = height ? height : img.height;
        let ctx = canvas.getContext("2d");
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        let dataURL = canvas.toDataURL();
        return dataURL;
      };
      let image = new Image();
      image.crossOrigin = "";
      image.src = img;
      if (img) {
        let getUrl = () => {
          return new Promise((resolve, reject) => {
            image.onload = () => {
              resolve(getBase64Image(image)); //将base64传给done上传处理
            };
          });
        };
        return await getUrl();
      }
    },
    /**
     * @description 更新ppt背景缩略图
     * @function updateSliderBackground
     * @return {type} {description}
     */
    updateSliderBackground: debounce(
        function ()  {
           let index=this.currentSlideIndex;
          html2canvas(document.querySelector(".pptEdit_main_editBox")).then(
            canvas => {
              this.sliderList[
                index
              ].backgroundImg = canvas.toDataURL();
            }
          );
        },
        1000,
        false
      )
  },
  mounted() {
    //   获取线上图片文件列表
    this.getImgList();
    let self=this;
    //监听全屏事件
    document.addEventListener("webkitfullscreenchange", self.fullscreenchange);
  }
};
</script>

